
.slider {
  position: relative;
  --auto-duration: 1000ms;
  overflow: hidden;
}

.slider__list {
  display: flex;
}

.slider__item {
  //width: 500px;
  flex-shrink: 0;
}

.slider__indicators {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 8px;
  left: 0;
  bottom: 13px;
  z-index: 9;
  text-align: center;
}

.slider__ind {
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background-color: rgba(72, 72, 72, 0.2);
}

.slider__ind:not(:first-child) {
  margin-left: 9px;
}

.slider__ind-color {
  width: 8px;
  height: 8px;
  background: $gradient;
}

.slider__list--click-duration {
  transition: 600ms ease-in-out;
}

.slider__list--auto-duration {
  //transition: var(--auto-duration, 2000ms) linear;
}

.slider__forward[disabled],
.slider__back[disabled] {
  opacity: 0.3;
  cursor: initial;
}

